.menu-wrapper{
    background-color: #16a085;
    padding:0px;
    /* height: 100px;*/
} 
.menu-wrapper ul li a{
    color: white;
    text-transform: uppercase;
}

.menu-wrapper ul li{
    padding: 30px 0px;
    font-size: 12px;
    width: 135px;
    text-align: center;
}
.menu-wrapper ul li:hover{
   background-color: #1abc9c;
}



